
import { defineComponent, reactive, toRefs } from "vue";

import pager from "./API/pager";

export default defineComponent({
  name: "Pagination",
  setup() {
    const data = reactive({
      breadcrumb: [],
    });
    const {
      paginator,
      perpage,
      page_num,
      page_count,
      submitPage,
      submitPerpage,
    } = pager();

    const refData = toRefs(data);
    return {
      ...refData,
      paginator,
      perpage,
      page_num,
      page_count,
      submitPage,
      submitPerpage,
    };
  },
});
